.chat {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  z-index: 50000;
  position: fixed;
  bottom: 0;
  right: 0;
  height: 75px;
  width: 75px;
  border: 5px solid black;
  background-color: orange;
}
.chatbox {
  display: none;
  position: fixed;
  z-index: 10;
  bottom: 0;
  right: 0;
}
.chatbox.open {
  display: block;
  height: 300px;
  width: 300px;
  border: 5px solid black;
  background-color: orange;
}
.chatMessages {
  height: 225px;
  background-color: lightcoral;
  overflow-y: scroll;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  color: black;
  font-size: 12px;
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

